import React from 'react';
import Action from '../actions/areaManagement';

export const heads = [
  'actions',
  'area',
  'rfc',
  'company_name',
  'status',
];

export const transmute = (data) => {
  const {
    area, rfc, companyName, status,
  } = data;

  return [
    <Action area={data} />,
    area,
    rfc,
    companyName,
    status,
  ];
};
