import React from 'react';
import RelatedDocs from 'scenes/common/related-docs';
import { useModal } from 'services/modal';

import { cfdiPropType } from 'helpers/propTypes';

import Template, { IconTypes } from './Template';

const Actions = ({ data = {} }) => {
  const { onOpenComponent } = useModal();
  const handleOpenRelatedDocs = () => onOpenComponent(() => <RelatedDocs doc={data} />);

  const { addressXml } = data;
  const realXMLPath = addressXml?.split(',')[0]; // TODO fix

  const iconsData = [
    { type: IconTypes.RELATED_DOCS, onClick: handleOpenRelatedDocs },
    { type: IconTypes.DOWNLOAD_PDF },
    { type: IconTypes.DOWNLOAD_XML, href: realXMLPath },
    { type: IconTypes.DOWNLOAD_ZIP },
  ];

  return (
    <Template icons={iconsData} />
  );
};

Actions.propTypes = {
  data: cfdiPropType,
};

export default Actions;
