import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { useI18n } from 'services/i18n';
import Icon from 'primitive-components/icon';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import { SvgAngleDownSolid, SvgAngleUpSolid } from 'react-line-awesome-svg';

import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
} from '@material-ui/core';

const Container = styled(Box)`
  box-shadow:${({ theme }) => theme.shadows.lg};
  margin-bottom: 2rem;
`;

const SummaryContainer = styled(Box)`
  display: flex;
  height: fit-content;
  align-items: center;
  color:${({ theme }) => theme.colors.accordionText};
  fill:${({ theme }) => theme.colors.accordionText};
  transition: 0.5s;
  padding: 0.5rem;
  &:hover {
    color:${({ theme }) => theme.colors.accordionTextHover};
    fill:${({ theme }) => theme.colors.accordionTextHover};
  }
`;

const AccordionCmp = ({ title, children, ...remainderProps }) => {
  const [isOpen, setIsOpen] = useState(false);
  const t = useI18n();
  return (
    <Container {...remainderProps}>
      <Accordion style={{ boxShadow: 'none' }}>
        <AccordionSummary onClick={() => setIsOpen(!isOpen)}>
          <SummaryContainer>
            <Icon
              svg={isOpen ? SvgAngleUpSolid : SvgAngleDownSolid}
              size={18}
            />
            <Text ml="0.7rem" fontSize="sm">
              {t(title)}
            </Text>
          </SummaryContainer>
        </AccordionSummary>
        <AccordionDetails>
          {children}
        </AccordionDetails>
      </Accordion>
    </Container>
  );
};

AccordionCmp.propTypes = {
  title: PropTypes.string,
  children: PropTypes.node.isRequired,
};

export default AccordionCmp;
