import React, { useEffect } from 'react';
import DataBox from 'components/data-box';
import Filter from 'components/filter';
import useSearch from 'services/search/useSearch';
import { useDispatch } from 'react-redux';
import { allowedSearchParams } from 'services/search/helpers';
import TableBox, { tableTypes } from 'components/table-box';
import {
  SvgPlusSolid,
  SvgUploadSolid,
  SvgDownloadSolid,
} from 'react-line-awesome-svg';
import {
  actions as docsActions,
  selectors as docsSelectors,
} from 'store/docs';

const AreaManagement = () => {
  const dispatch = useDispatch();
  const { search, params, updateParams } = useSearch();
  const {
    area, rfc,
  } = params;

  const headBtnsConfig = [
    { id: 'new', design: 'primary', svgIcon: SvgPlusSolid },
    { id: 'import', design: 'default', svgIcon: SvgUploadSolid },
    { id: 'export', design: 'info', svgIcon: SvgDownloadSolid },
  ];

  const areas = [{
    area: 'Contabilidad',
    rfc: 'XAXX010101000',
    companyName: 'Banco regional de Mo...',
    status: 'Área activa',
  },
  {
    area: 'Desarrollo',
    rfc: 'XAXX010101000',
    companyName: 'Desarrollo',
    status: 'Área activa',
  }];

  const filtersConfig = [
    {
      label: 'area',
      enableSearch: true,
      items: [], // TODO: Poner aqui los items desde el back
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_RECEIVER,
    },
    {
      label: 'rfcs',
      enableSearch: true,
      items: [], // TODO: Poner aqui los items desde el back
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_ISSUER,
    },

  ];

  const handleChangeFilters = (filters) => updateParams(filters);

  return (
    <DataBox title="area_management" separator headBtnsConfig={headBtnsConfig}>
      <Filter
        config={filtersConfig}
        selected={{
          area,
          rfc,
        }}
        onSubmit={handleChangeFilters}
      />

      <TableBox
        tableType={tableTypes.AREA_MANAGEMENT}
        elements={areas}
        noFoundMessage="No hay areas"
        isBusy={false}
        onChangePage={() => {}}
        totalPages={1}
        page={1}
      />
    </DataBox>
  );
};

export default AreaManagement;
