import React, { useState } from 'react';

import FileInput from 'components/file-input';
import { Grid } from '@material-ui/core';
import { DataList, FileListItem } from 'components/data-list';

const FileUploader = () => {
  const [pdfFiles, setPdfFiles] = useState([]);
  const [xmlFiles, setXmlFiles] = useState([]);

  const handleSelectedFiles = (e) => {
    const files = [...e.target.files];
    const xml = files.filter((f) => f.name.endsWith('.xml'));
    const pdf = files.filter((f) => f.name.endsWith('.pdf'));

    setXmlFiles(xml);
    setPdfFiles(pdf);
  };

  const handleDeleteXmlFile = (name) => {
    setXmlFiles(xmlFiles.filter((f) => f.name.localeCompare(name) !== 0));
  };

  const handleDeletePdfFile = (name) => {
    setPdfFiles(pdfFiles.filter((f) => f.name.localeCompare(name) !== 0));
  };

  return (
    <Grid container spacing={4}>
      <Grid item xs={12}>
        <FileInput
          multiple
          accept="application/xml,application/pdf"
          onChange={handleSelectedFiles}
        />
      </Grid>
      <Grid item xs={12} md={6}>
        {xmlFiles && (
        <DataList maxHeight={300}>
          {xmlFiles.map((item) => <FileListItem key={item.name} filename={item.name} size={item.size} onDelete={() => handleDeleteXmlFile(item.name)} />)}
        </DataList>
        )}
      </Grid>
      <Grid item xs={12} md={6}>
        {pdfFiles && (
        <DataList maxHeight={300}>
          {pdfFiles.map((item) => <FileListItem key={item.name} filename={item.name} size={item.size} onDelete={() => handleDeletePdfFile(item.name)} />)}
        </DataList>
        )}
      </Grid>
    </Grid>
  );
};

export default FileUploader;
