import React from 'react';
import { purchaseOrderPropType } from 'helpers/propTypes';

import Template, { IconTypes } from './Template';

const Actions = ({ data = {} }) => {
  const iconsData = [
    { type: IconTypes.RELATED_DOCS },
  ];

  return (
    <Template icons={iconsData} />
  );
};

Actions.propTypes = {
  data: purchaseOrderPropType,
};

export default Actions;
