import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useI18n } from 'services/i18n';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import Checkbox from 'primitive-components/checkbox';
import Input from 'components/input';
import styled from 'styled-components';
import Popover from '@material-ui/core/Popover';

const Container = styled(Box)`
  margin-bottom: 2rem;
  padding: 0px 15px;
`;

const Label = styled(Text)`
  white-space: nowrap;
`;

const InputContainer = styled(Box)`
  margin-top: 10px;
  background-color: ${({ theme }) => theme.colors.dropdownSelectBackground};
  border-radius: 5rem;
  padding: 10px 20px;
  cursor: pointer;
  color: ${({ theme, hasSelectedItems }) => theme.colors[hasSelectedItems ? 'dropdownSelectTextSelected' : 'dropdownSelectText']};
`;

const InputText = styled(Text)`
  min-width: 7rem;
  white-space: nowrap;
`;

const DownTriangle = styled(Text)`
 &::after {
    content: "▾";
  }
`;

const ItemContainer = styled(Box)`
  margin: 0.5rem;
  display: flex;
  align-items: center;
  cursor: pointer;
`;

const Scrollable = styled(Box)`
  overflow-y: auto;
  max-height: 12rem;
`;

const defaultSelectedItems = [];

const DropDownSelect = ({
  label,
  enableSearch,
  items,
  onChange,
  value,
  fieldName,
  ...remainderProps
}) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const [selectedItems, setSelectedItems] = useState(defaultSelectedItems);
  const [searchText, setSearchText] = useState('');
  const t = useI18n();

  const handleOpen = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  const handleSelect = (select) => {
    const exist = selectedItems.includes(select);
    const updatedSelectedItems = exist
      ? selectedItems.filter((item) => item !== select)
      : [...selectedItems, select];
    setSelectedItems(updatedSelectedItems);

    onChange?.({ [fieldName]: updatedSelectedItems });
  };

  const getInputText = () => {
    const { length } = selectedItems;
    if (length === 0) return t('nothing_selected');
    return length === 1 ? selectedItems[0] : t('selected_items', { length });
  };

  useEffect(() => {
    if (anchorEl) setSearchText('');
  }, [anchorEl]);

  useEffect(() => {
    setSelectedItems(defaultSelectedItems);
  }, [items]);

  useEffect(() => {
    setSelectedItems(value || defaultSelectedItems);
  }, [value]);

  return (
    <Container {...remainderProps}>
      <Label fontSize="sm" mb="0.5rem">{t(label)}</Label>
      <InputContainer
        centeredContent
        fontSize="xs"
        fontWeight="bld"
        onClick={handleOpen}
        hasSelectedItems={!!selectedItems.length}
      >
        <InputText>{getInputText()}</InputText>
        <DownTriangle />
      </InputContainer>

      {!!items?.length && (
        <Popover
          anchorEl={anchorEl}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'center',
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'center',
          }}
          open={Boolean(anchorEl)}
          onClose={handleClose}
        >
          <Scrollable>
            {enableSearch && (
              <Input
                value={searchText}
                onChange={(e) => setSearchText(e.target.value)}
              />
            )}
            {items.map((item, index) => {
              if (!item.startsWith(searchText)) return null;
              return (
                <ItemContainer key={index} onClick={() => handleSelect(item)}>
                  <Text fontSize="sm">{item}</Text>
                  <Checkbox
                    ml="auto"
                    backgroundColor="transparent"
                    checked={selectedItems.includes(item)}
                  />
                </ItemContainer>
              );
            })}
          </Scrollable>
        </Popover>
      )}
    </Container>
  );
};

DropDownSelect.propTypes = {
  fieldName: PropTypes.string.isRequired,
  items: PropTypes.arrayOf(PropTypes.string),
  onChange: PropTypes.func,
  value: PropTypes.arrayOf(PropTypes.string),
  label: PropTypes.string,
  enableSearch: PropTypes.bool,
};

export default DropDownSelect;
