import React from 'react';
import styled from 'styled-components';
import Box from '../box';

const StyledSeparator = styled(Box)`
  height:0;
  border: none;
  border-top: solid 0.07rem rgba(0,0,0,0.05);
`;

const Separator = (props) => <StyledSeparator {...props} />;

export default Separator;
