import React from 'react';
import PropTypes from 'prop-types';
import Text from 'primitive-components/text';
import Box from 'primitive-components/box';
import Input from 'primitive-components/input';
import styled from 'styled-components';
import { styledSystem } from 'services/theme/helpers';

const Container = styled(Box)`
    display: flex;
    flex-direction: column;
    padding: 0 15px;
    margin-bottom: 14px;
    ${styledSystem};
`;

const Label = styled(Text)`
    margin-bottom: 7px;
`;

const StyledInput = styled(Input)`
   padding: 0.5rem;
   border-radius: 4px;
   border: ${({ theme }) => `solid 1px ${theme.colors.inputBorder || '#000000'}`};
   color: ${({ theme }) => theme.colors.inputText || '#000000'};
   line-height: 1.25;
   transition: border-color .15s ease-in-out;
   &:focus {
       border-color:${({ theme }) => theme.colors.inputBorderFocus};
       outline: none;
   }
`;

const InputC = ({
  type, label, value, onChange, onFocus, onBlur, onClick, readOnly, ...remainderProps
}) => (
  <Container {...remainderProps}>
    <Label fontSize="sm">{label}</Label>
    <StyledInput
      type={type}
      readOnly={readOnly}
      value={value}
      onChange={onChange}
      onFocus={onFocus}
      onBlur={onBlur}
      onClick={onClick}
    />
  </Container>
);

InputC.propTypes = {
  type: PropTypes.string,
  label: PropTypes.string,
  value: PropTypes.string,
  onChange: PropTypes.func,
  onFocus: PropTypes.func,
  onBlur: PropTypes.func,
  onClick: PropTypes.func,
  readOnly: PropTypes.bool,
};

export default InputC;
