import React from 'react';
import Button from 'components/button';
import Action from '../actions/providerManagement';

export const heads = [
  'actions',
  'number',
  'rfc',
  'user',
  'social_reason',
  'email',
  'active',
  'blocked',
];

export const transmute = (data) => {
  const {
    number, rfc, name, email, blocked, status,
  } = data;

  return [
    <Action data={data} />,
    number,
    rfc,
    name,
    'ni idea',
    email,
    status,
    blocked
      ? <Button rounded design="primary">Si</Button>
      : <Button rounded design="error">No</Button>,
  ];
};
