import React, { useState, useEffect } from 'react';
import { useI18n } from 'services/i18n';
import PropTypes from 'prop-types';
import { addYears } from 'date-fns';
import { DateRange as DateRangeC } from 'react-date-range';
import Input from 'components/input';
import Popover from '@material-ui/core/Popover';

import Box from 'primitive-components/box';
import styled from 'styled-components';
import { ToShortDate } from 'helpers/date';

import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file

const DateRangeContainer = styled(Box)`
    border: solid 1px gray;
    border-radius: 4px;
    padding: 1rem;
    background-color: white;
`;

const RangeToSrt = ({ startDate, endDate }) => `${ToShortDate(startDate, 'date')} - ${ToShortDate(endDate, 'date')}`;

const toDateRange = ({ dateIn, dateEnd }) => ({
  startDate: dateIn ? new Date(dateIn) : addYears(new Date(), -20), // Last 20 years
  endDate: dateEnd ? new Date(dateEnd) : new Date(),
  key: 'selection',
});

const fromDateRange = ({ endDate, startDate }) => ({
  dateIn: startDate.toISOString(),
  dateEnd: endDate.toISOString(),
});

const DateRange = ({ value, onChange, ...remainigProps }) => {
  const [range, setRange] = useState();
  const [anchorEl, setAnchorEl] = useState(null);
  const t = useI18n();

  const handleOpen = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  const handleOnChange = ({ selection }) => {
    setRange(selection);
    onChange?.(fromDateRange(selection));
  };

  useEffect(() => {
    setRange(toDateRange(value));
  }, [value]);

  if (!range) return null;

  return (
    <>
      <Input
        label={t('date')}
        value={RangeToSrt(range)}
        readOnly
        onClick={handleOpen}
        {...remainigProps}
      />
      <Popover
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <DateRangeContainer>
          <DateRangeC
            editableDateInputs
            onChange={handleOnChange}
            moveRangeOnFirstSelection={false}
            ranges={[range]}
          />
        </DateRangeContainer>
      </Popover>
    </>
  );
};

DateRange.propTypes = {
  onChange: PropTypes.func,
  value: PropTypes.shape({
    dateIn: PropTypes.string,
    dateEnd: PropTypes.string,
  }),
};

export default DateRange;
