import React from 'react';
import Button from 'components/button';
import Action from '../actions/providerManagement';

export const heads = [
  'actions',
  'serie',
  'folio',
  'date',
  'rfcIssuer',
  'rfcReceiver',
  'status',
  'total',
  'uuid_fiscal_folio',
];

export const transmute = (data) => {
  const {
    serie, folio, rfcIssuer, rfcReceiver, status, total, uuid,
  } = data;

  return [
    <Action data={data} />,
    serie, folio, rfcIssuer, rfcReceiver, status, total, uuid,
  ];
};
