import { NAME } from './constants';

export const FETCH_DOCS = `${NAME}.FETCH_DOCS`;
export const FetchDocs = (data) => ({
  type: FETCH_DOCS,
  payload: data,
});

export const FETCH_DOCS_BUSY = `${NAME}.FETCH_DOCS_BUSY`;
export const FetchDocsBusy = () => ({
  type: FETCH_DOCS_BUSY,
  payload: null,
});

export const FETCH_DOCS_FAILED = `${NAME}.FETCH_DOCS_FAILED`;
export const FetchDocsFailed = () => ({
  type: FETCH_DOCS_FAILED,
  payload: null,
});

export const FETCH_DOCS_SUCCESS = `${NAME}.FETCH_DOCS_SUCCESS`;
export const FetchDocsSuccess = (data) => ({
  type: FETCH_DOCS_SUCCESS,
  payload: data,
});
