import qs from 'query-string';
import { splitCommaSeparatedArray } from 'helpers/general';
import _ from 'lodash';

export const DEFAULT_PAGINATION = { page: 1 };

export const allowedSearchParams = {
  RFC_ISSUER: 'rfcIssuer',
  RFC_RECEIVER: 'rfcReceiver',
  PAGE: 'page',
  DATE_END: 'dateEnd',
  DATE_IN: 'dateIn',
  FOLIO: 'folio',
  TYPE_DOC: 'typedoc',
  UUID: 'uuid',
  PURCHASE_ORDER: 'noOrdenC',
  KEY_SORT: 'keySort',
  TOTAL: 'total',
  ACED_NECK: 'acedNeck',
  STATE: 'state',
  ROLE: 'role',
};

export const valuesAllowedParams = Object.values(allowedSearchParams);

export const getPropHasChanged = (props = {}, newProps = {}) => (params) => _.some(params, (param) => props[param] !== newProps[param]);

// export const getPropHasChanged = (props = {}, newProps = {}) => (prop) => {
//   if (!valuesAllowedParams.includes(prop)) {
//     return false;
//   }
//   return props[prop] !== newProps[prop];
// };

const fromEntries = (arr) => arr.reduce((p, [key, v]) => ({ ...p, [key]: v }), {});

const isEmptyArray = (value) => (Array.isArray(value) ? _.isEmpty(value) : false);

export const removeFalsyValues = (params) => fromEntries(
  Object.entries(params)
    .filter(
      ([key, value]) => valuesAllowedParams.includes(key) && value && !isEmptyArray(value),
    ),
);

export const encodeSearch = (search) => encodeURIComponent(search);
export const decodeSearch = (search) => decodeURIComponent(search);

export const getValidProps = (props) => {
  const validProps = { ...props };
  if (props.page) validProps.page = Number(props.page);
  if (props.rfcIssuer) validProps.rfcIssuer = splitCommaSeparatedArray(validProps.rfcIssuer);
  if (props.rfcReceiver) validProps.rfcReceiver = splitCommaSeparatedArray(validProps.rfcReceiver);
  return validProps;
};

export const paramsToSearch = (params) => qs.stringify(params, { arrayFormat: 'comma' });
export const searchToParams = (search) => getValidProps(qs.parse(search));

export const transmuteSearchCheckingRole = (lineSearch) => {
  const params = searchToParams(lineSearch);
  if (params.role) {
    const search = {};
    Object.keys(params).forEach((key) => {
      if (key === 'role') return;
      const roleParams = {
        ...DEFAULT_PAGINATION,
        ...searchToParams(decodeSearch(params[key])),
      };

      params[key] = roleParams;
      search[key] = encodeSearch(paramsToSearch(roleParams));
    });
    return { params, search };
  }

  const newParams = { ...DEFAULT_PAGINATION, ...params };
  const newSearch = paramsToSearch(newParams);
  return { params: newParams, search: newSearch };
};

export const paramsToSearchCheckingRole = (params) => {
  const paramsT = { ...params };
  if (params.role) {
    Object.keys(params).forEach((key) => {
      if (key === 'role') return;
      paramsT[key] = encodeSearch(paramsToSearch(params[key]));
    });
  }
  return paramsToSearch(paramsT);
};

export const searchFromRoles = (roles) => { // roles's type is [<string>]
  if (!roles) return null;

  let params = { role: true };
  roles.forEach((role) => {
    params = { ...params, [role]: DEFAULT_PAGINATION };
  });

  return paramsToSearchCheckingRole(params);
};
