import { createSelector } from 'reselect';
import { NAME } from './constants';

import {
  getIsNotStarted,
  getIsBusy,
  getIsSuccess,
  getIsFailed,
} from './status';

const getFetchedData = (state) => state[NAME].data || {};

const getDocs = createSelector(
  getFetchedData,
  (data) => data?.list || [],
);

const getReports = createSelector(
  getFetchedData,
  (data) => data?.reports || {},
);

const getTotalPages = createSelector(
  getFetchedData,
  (data) => data?.pages,
);

