export const getConfigForStateDocuments = ({ report, t, theme }) => {
  const { state } = report;

  return {
    type: 'pie',
    data: [
      {
        title: 'Status',
        backgroundColor: [theme.colors.chartSuccess, theme.colors.chartError],
        data: [state.Recibido, state['Con Error']],
        labels: ['Recibidos', 'Error'],
      },
    ],
    options: {
      scales: {
        xAxes: [
          {
            gridLines: {
              display: false,
            },
          },
        ],
        yAxes: [
          {
            gridLines: {
              display: false,
            },
          },
        ],
      },
    },
  };
};
