import { takeEvery, call, put } from 'redux-saga/effects';
import { checkDefaultParams } from '../commonSagas';

import * as actions from './actions';

function* fetchDocs(api, { payload: params }) {
  try {
    const updatedParams = yield call(checkDefaultParams, params);

    yield put(actions.FetchDocsBusy());
    const { data } = yield call(api.searchDocs, updatedParams);
    yield put(actions.FetchDocsSuccess(data));
  } catch (e) {
    yield put(actions.FetchDocsFailed());
  }
}

export default function* saga({ api }) {
  yield takeEvery(actions.FETCH_DOCS, fetchDocs, api);
}
