import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import {
  SvgShareSolid,
  SvgFilePdfSolid,
} from 'react-line-awesome-svg';
import useSelectors from 'helpers/useSelectors';
import { actions as docsActions, selectors as docsSelectors } from 'store/docs';
import { selectors as authSelectors } from 'store/auth';
import { paramsToFetchReports } from 'store/contants';
import useSearch from 'services/search/useSearch';
import { allowedSearchParams } from 'services/search/helpers';
import Page from 'components/page';
import Filter from 'components/filter';
import DataBox from 'components/data-box';
import { CfdisPurchaseOrders, KPITotalsStatus } from 'components/reports';

const Reports = () => {
  const dispatch = useDispatch();
  const [
    rfcsIssuer,
    rfcsReceiver,
    reports,
    isBusy,
  ] = useSelectors(
    authSelectors.getRFCsIssuer,
    authSelectors.getRFCsReceiver,
    docsSelectors.getReports,
    docsSelectors.getIsBusy,
  );

  const { search, params, updateParams } = useSearch();
  const {
    rfcIssuer, rfcReceiver, dateEnd, dateIn,
  } = params;

  const FetchDocs = () => dispatch(docsActions.FetchDocs({ ...params, ...paramsToFetchReports }));

  const handleChangeFilters = (filters) => updateParams(filters);

  useEffect(() => {
    FetchDocs();
  }, [search]);

  const filtersConfig = [
    {
      label: 'issuing_rfc',
      enableSearch: true,
      items: rfcsIssuer,
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_ISSUER,
    },
    {
      label: 'receiver_rfc',
      enableSearch: true,
      items: rfcsReceiver,
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_RECEIVER,
    },
    {
      type: 'date',
    },
  ];

  const headBtnsConfig = [
    { id: 'share', design: 'default', svgIcon: SvgShareSolid },
    { id: 'download_pdf', design: 'default', svgIcon: SvgFilePdfSolid },
  ];

  return (
    <Page title="reports">

      <DataBox title="filters" separator headBtnsConfig={headBtnsConfig}>
        <Filter
          config={filtersConfig}
          selected={{
            rfcIssuer,
            rfcReceiver,
            dateEnd,
            dateIn,
          }}
          onSubmit={handleChangeFilters}
        />
      </DataBox>

      <KPITotalsStatus isBusy={isBusy} reports={reports} />

      <CfdisPurchaseOrders isBusy={isBusy} reports={reports} />
    </Page>
  );
};

export default Reports;
