import { createSelector } from 'reselect';
import { NAME } from './constants';
import {
  getIsAuthBusy,
  getIsAuthFailed,
  getIsAuthNotStarted,
  getIsAuthSuccess,
} from './status';

const getRFCsIssuer = (state) => state[NAME].rfcsIssuer || [];
const getRFCsReceiver = (state) => state[NAME].rfcsReceiver || [];

const getAuthUser = (state) => state[NAME].user;
const getAuthErrorMessage = (state) => state[NAME].error?.message;

const getAccessLevel = (state) => state[NAME].authData?.accessLevel;
const getAuthToken = (state) => state[NAME].authData?.token;

const getIsAuthenticated = createSelector(
  getAuthToken,
  (authtoken) => !!authtoken,
);

const getAuthKeepSession = (state) => state[NAME].keepSession;

export {
  getRFCsIssuer,
  getRFCsReceiver,

  getAuthUser,
  getIsAuthBusy,
  getIsAuthFailed,
  getIsAuthNotStarted,
  getIsAuthSuccess,
  getAuthErrorMessage,
  getIsAuthenticated,
  getAuthToken,
  getAuthKeepSession,
  getAccessLevel,
};
