import {
  reducer as reducerRS,
  name as nameRS,
  defaultStatus as defaultStatusRS,
} from './status';
import * as actions from './actions';
import {
  defaultUser,
  defaultAuthData,
  defaultError,
  defaultKeepSession,
  defaultRFCsIssuer,
  defaultRFCsReceiver,
} from './constants';

const defaultFormState = {
  user: defaultUser,
  error: defaultError,
  keepSession: defaultKeepSession,
};

const defaultState = {
  ...defaultFormState,
  rfcsIssuer: defaultRFCsIssuer,
  rfcsReceiver: defaultRFCsReceiver,
  authData: defaultAuthData,
  [nameRS]: defaultStatusRS,
};

const reducer = (stateP = defaultState, action = {}) => {
  const state = { ...stateP, [nameRS]: reducerRS(stateP[nameRS], action) };

  switch (action.type) {
    case actions.LOGIN_SUCCESS:
      return {
        ...state,
        ...defaultFormState,
        authData: action.payload,
      };
    case actions.LOAD_SESSION_LS_SUCCESS:
      return {
        ...state,
        authData: action.payload,
      };
    case actions.REGISTER_SUCCESS:
      return {
        ...state,
        ...defaultFormState,
      };
    case actions.LOGIN_FAILED:
    case actions.REGISTER_FAILED:
      return {
        ...state,
        error: action.payload,
      };
    case actions.CHANGE_USER:
      return {
        ...state,
        user: { ...state.user, ...action.payload },
      };
    case actions.LOGOUT:
      return {
        ...state,
        authData: defaultAuthData,
      };
    case actions.CHANGE_KEEP_SESSION:
      return {
        ...state,
        keepSession: action.payload,
      };
    default:
      return state;
  }
};

export default reducer;
