import { PATH as pathProviders } from 'scenes/providers';
import { PATH as pathUser } from 'scenes/user';
import { accessLevels } from './constants';

const redirects = {
  [accessLevels.PROVIDERS]: pathProviders,
  [accessLevels.USER]: pathUser,
};

export const accessLevelToRoute = (accessLevel) => redirects[accessLevel];
