import React from 'react';
import PropTypes from 'prop-types';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';

import { push, getLocation } from 'connected-react-router';
import { useDispatch, useSelector } from 'react-redux';
import { useI18n } from 'services/i18n';

import styled, { useTheme } from 'styled-components';
import Icon from 'primitive-components/icon';

const Container = styled(Box)`
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-top: 1.5rem;
  transition: 0.5s all ease-in-out;
  &:hover {
    svg {
      fill: ${({ theme }) => theme.colors.sideBarIconActive};
    }
  }
`;

const Item = ({ item }) => {
  const { icon: Svg, id, route } = item;
  const t = useI18n();
  const theme = useTheme();
  const dispatch = useDispatch();
  const { pathname } = useSelector(getLocation);
  const isActive = pathname === route;

  const handleOnClick = () => dispatch(push(route));

  const iconColor = theme.colors[`${isActive ? 'sideBarIconActive' : 'sideBarIconInactive'}`];

  return (
    <Container onClick={handleOnClick}>
      <Icon svg={Svg} color={iconColor} size={28} />
      <Text fontSize="md" ml="1rem" color={theme.colors.sideBarText}>
        {t(id)}
      </Text>
    </Container>
  );
};

Item.propTypes = {
  item: PropTypes.shape({
    id: PropTypes.string,
    route: PropTypes.string,
    icon: PropTypes.func,
    subs: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.string,
        route: PropTypes.string,
      }),
    ),
  }),
};

export default Item;
