import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import useSearch from 'services/search/useSearch';
import {
  SvgSyncSolid,
} from 'react-line-awesome-svg';
import {
  actions as pcsActions,
  selectors as pcsSelectors,
} from 'store/pc';
import { paramsToFetchPC, cfdiStates } from 'store/contants';

import { selectors as authSelectors } from 'store/auth';

import DataBox from 'components/data-box';
import Accordion from 'primitive-components/accordion';
import Filter from 'components/filter';
import TableBox, { tableTypes } from 'components/table-box';
import { allowedSearchParams } from 'services/search/helpers';
import { roles } from './constants';

const role = roles[1];

const PCsTable = () => {
  const dispatch = useDispatch();
  const { search: { [role]: search = '' }, params: { [role]: params = {} }, updateParams: updateParamsT } = useSearch();
  const updateParams = (p) => updateParamsT(p, role);

  const {
    page, rfcIssuer, rfcReceiver, dateEnd, dateIn,
  } = params;

  const [
    rfcsIssuer,
    rfcsReceiver,
    pcs,
    isBusy,
    totalPages,
  ] = useSelectors(
    authSelectors.getRFCsIssuer,
    authSelectors.getRFCsReceiver,
    pcsSelectors.getPCs,
    pcsSelectors.getIsBusy,
    pcsSelectors.getTotalPages,
  );

  const FetchPCs = () => dispatch(pcsActions.FetchPCs({ ...params, ...paramsToFetchPC }));

  const handleChangePage = (pageT) => updateParams({ page: pageT });
  const handleChangeFilters = (filters) => updateParams(filters);

  useEffect(() => {
    FetchPCs();
  }, [search]);

  const filtersConfig = [
    {
      label: 'receiver_rfc',
      enableSearch: true,
      items: rfcsReceiver,
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_RECEIVER,
    },
    {
      label: 'issuing_rfc',
      enableSearch: true,
      items: rfcsIssuer,
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_ISSUER,
    },
    {
      label: 'state',
      enableSearch: true,
      items: Object.values(cfdiStates),
      type: 'dropdown',
      fieldName: allowedSearchParams.STATE,
    },
    {
      type: 'date',
    },
    {
      label: 'uuid_fiscal_folio',
      enableSearch: true,
      type: 'dropdown',
      fieldName: 'folio',
    },
  ];

  const headBtnsConfig = [
    {
      id: 'update', design: 'shadow', svgIcon: SvgSyncSolid, customStyles: { rounded: true },
    },
  ];

  return (
    <DataBox
      title="payment_complement_list"
      headBtnsConfig={headBtnsConfig}
      separator
    >
      <Accordion title="filter_search">
        <Filter
          config={filtersConfig}
          selected={{
            rfcIssuer,
            rfcReceiver,
            dateEnd,
            dateIn,
          }}
          onSubmit={handleChangeFilters}
        />
      </Accordion>

      <TableBox
        tableType={tableTypes.CFDI}
        elements={pcs}
        noFoundMessage="no_payment_complements_found"
        isBusy={isBusy}
        onChangePage={handleChangePage}
        totalPages={totalPages}
        page={page}
      />

    </DataBox>
  );
};

export default PCsTable;
