import {
  takeEvery, call, put, select,
} from 'redux-saga/effects';
import { save as saveLS, load as loadLS, remove as removeLS } from 'helpers/localStorage';
import { NAME_AUTHDATA_LS, accessLevels } from './constants';
import { getAuthKeepSession } from './selectors';
import * as actions from './actions';

const authDataFromServer = (data) => ({
  accessLevel: accessLevels.USER,
  token: 'cacacaca',
  ...data,
});

function* loginSubmit(api, { payload: user }) {
  try {
    yield put(actions.LoginBusy());
    const response = yield call(api.signIn, user);

    const authData = authDataFromServer(response);

    const keepSession = yield select(getAuthKeepSession);
    if (keepSession) saveLS(NAME_AUTHDATA_LS, authData);

    yield put(actions.LoginSuccess(authData));
  } catch (e) {
    console.error(e);
    yield put(actions.LoginFailed(e));
  }
}

function* logout(api) {
  try {
    yield call(api.signOut);
    yield removeLS(NAME_AUTHDATA_LS);
  } catch (e) {
    console.error(e);
  }
}

function* starting() {
  const authData = loadLS(NAME_AUTHDATA_LS);
  if (authData) {
    yield put(actions.LoadSessionLsSuccess(authData));
  }
}

function* registerSubmit(api, { payload: user }) {
  try {
    yield put(actions.RegisterBusy());
    const { data } = yield call(api.signUp, user);
    yield put(actions.RegisterSuccess(data));
  } catch (e) {
    console.error(e);
    yield put(actions.RegisterFailed(e));
  }
}

export default function* loginSaga({ api }) {
  yield takeEvery('STARTING', starting);
  yield takeEvery(actions.LOGIN_SUBMIT, loginSubmit, api);
  yield takeEvery(actions.LOGOUT, logout, api);
  yield takeEvery(actions.REGISTER_SUBMIT, registerSubmit, api);
}
