import React, { useEffect } from 'react';
import DataBox from 'components/data-box';
import Filter from 'components/filter';
import useSearch from 'services/search/useSearch';
import { useDispatch } from 'react-redux';
import { allowedSearchParams } from 'services/search/helpers';
import TableBox, { tableTypes } from 'components/table-box';
import {
  SvgPlusSolid,
  SvgUploadSolid,
  SvgDownloadSolid,
} from 'react-line-awesome-svg';
import {
  actions as docsActions,
  selectors as docsSelectors,
} from 'store/docs';

const UserManagement = () => {
  const dispatch = useDispatch();
  const { search, params, updateParams } = useSearch();
  const {
    area, rfc,
  } = params;

  const headBtnsConfig = [
    { id: 'new', design: 'primary', svgIcon: SvgPlusSolid },
    { id: 'import', design: 'default', svgIcon: SvgUploadSolid },
    { id: 'export', design: 'info', svgIcon: SvgDownloadSolid },
  ];

  const users = [{
    number: 1,
    name: 'Juan P',
    email: 'aaa@gmail.com',
    profile: 'Administrador',
    area: 'Contabilidad',
    status: 'Activo',
  },
  {
    number: 2,
    name: 'Luis  P',
    email: 'bbb@gmail.com',
    profile: 'Administrador',
    area: 'Desarrollo',
    status: 'Inactivo',
  }];

  const filtersConfig = [
    {
      label: 'name',
      enableSearch: true,
      items: [], // TODO: Poner aqui los items desde el back
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_RECEIVER,
    },
    {
      label: 'email',
      enableSearch: true,
      items: [], // TODO: Poner aqui los items desde el back
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_ISSUER,
    },
    {
      label: 'profile',
      enableSearch: true,
      items: [], // TODO: Poner aqui los items desde el back
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_ISSUER,
    },
  ];

  const handleChangeFilters = (filters) => updateParams(filters);

  return (
    <DataBox title="user_management" separator headBtnsConfig={headBtnsConfig}>
      <Filter
        config={filtersConfig}
        selected={{
          area,
          rfc,
        }}
        onSubmit={handleChangeFilters}
      />

      <TableBox
        tableType={tableTypes.USER_MANAGEMENT}
        elements={users}
        noFoundMessage="No hay usuarios"
        isBusy={false}
        onChangePage={() => {}}
        totalPages={1}
        page={1}
      />
    </DataBox>
  );
};

export default UserManagement;
