import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import {
  actions as docsActions,
  selectors as docsSelectors,
} from 'store/docs';

import { selectors as authSelectors } from 'store/auth';
import { purchaseOrderStates, docsTypes } from 'store/contants';
import useSearch from 'services/search/useSearch';
import {
  SvgPlusSolid,
  SvgUploadSolid,
  SvgDownloadSolid,
} from 'react-line-awesome-svg';
import Accordion from 'primitive-components/accordion';
import Page from 'components/page';
import { allowedSearchParams } from 'services/search/helpers';
import DataBox from 'components/data-box';
import Filter from 'components/filter';
import TableBox, { tableTypes } from 'components/table-box';

const fixedParams = {
  [allowedSearchParams.TYPE_DOC]: docsTypes.PURCHASE,
};

const PurchaseOrders = () => {
  const dispatch = useDispatch();
  const { search, params, updateParams } = useSearch();
  const {
    page, rfcIssuer, rfcReceiver, dateEnd, dateIn,
  } = params;

  const [
    rfcsIssuer,
    rfcsReceiver,
    purchaseOrders,
    isBusy,
    totalPages,
  ] = useSelectors(
    authSelectors.getRFCsIssuer,
    authSelectors.getRFCsReceiver,
    docsSelectors.getDocs,
    docsSelectors.getIsBusy,
    docsSelectors.getTotalPages,
  );

  const FetchDocs = () => dispatch(docsActions.FetchDocs({ ...params, ...fixedParams }));

  const handleChangePage = (pageT) => updateParams({ page: pageT });
  const handleChangeFilters = (filters) => updateParams(filters);

  useEffect(() => {
    FetchDocs();
  }, [search]);

  const filtersConfig = [
    {
      label: 'total',
      type: 'dropdown',
      fieldName: 'caca', // TODO
    },
    {
      label: 'document_number',
      enableSearch: true,
      // items: rfcs,
      type: 'dropdown',
      fieldName: 'caca', // TODO
    },
    {
      type: 'date',
    },
    {
      label: 'issuing_rfc',
      enableSearch: true,
      items: rfcsIssuer,
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_ISSUER,
    },
    {
      label: 'receiver_rfc',
      enableSearch: true,
      items: rfcsReceiver,
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_RECEIVER,
    },
    {
      label: 'state',
      enableSearch: true,
      items: Object.values(purchaseOrderStates),
      type: 'dropdown',
      fieldName: allowedSearchParams.STATE,
    },
  ];

  const headBtnsConfig = [
    { id: 'new', design: 'primary', svgIcon: SvgPlusSolid },
    { id: 'import', design: 'default', svgIcon: SvgUploadSolid },
    { id: 'export', design: 'info', svgIcon: SvgDownloadSolid },
  ];

  return (
    <Page title="purchase_orders">
      <DataBox
        title="purchase_order"
        headBtnsConfig={headBtnsConfig}
        separator
      >
        <Accordion title="filter_search">
          <Filter
            config={filtersConfig}
            selected={{
              rfcIssuer,
              rfcReceiver,
              dateEnd,
              dateIn,
            }}
            onSubmit={handleChangeFilters}
          />
        </Accordion>

        <TableBox
          tableType={tableTypes.PURCHASE_ORDERS}
          elements={purchaseOrders}
          noFoundMessage="no_purchase_orders_found"
          isBusy={isBusy}
          onChangePage={handleChangePage}
          totalPages={totalPages}
          page={page}
        />
      </DataBox>
    </Page>

  );
};

export default PurchaseOrders;
