import React from 'react';
import styled from 'styled-components';
import { layout } from 'styled-system';
import { useI18n } from 'services/i18n';
import useSelectors from 'helpers/useSelectors';
import { Redirect } from 'react-router';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import LoginForm from 'components/login-form';
import SrcBackground from 'media/01.jpg';
import Logo from 'components/logo';
import { selectors as authSelectors } from 'store/auth';
import { accessLevelToRoute } from 'store/auth/helpers';
import Spinner from './Spinner';

const BackGroundImage = styled(Box)`
    background-image: url(${SrcBackground});
    background-size: cover;
    height: 100%;
    width:100%;
    ${layout}
`;

const WelcomeContainer = styled(Box)`
    background: linear-gradient(135deg,rgba(46,52,81,.2) 0%,rgba(46,52,81,.95) 100%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
    width: 100%;
    height: 100%;
    padding: 0 2.5rem;
`;

const WelcomeTitle = styled(Text)`
    line-height: 1.1;
`;

const WelcomeSubTitle = styled(Text)`
    padding-top: 15px;
    line-height: 1.5;
`;

const FormContainer = styled(Box)`
    flex-direction: column;
    height: 100%;
    padding: 0 3rem;
    ${layout}
`;

const LoginLabel = styled(Box)`
  margin-bottom: 30px;
  width:100%;
`;

const { getAccessLevel, getIsAuthBusy } = authSelectors;

export default () => {
  const t = useI18n();
  const [
    authAccessLevel,
    isBusy,
  ] = useSelectors(
    getAccessLevel,
    getIsAuthBusy,
  );

  if (authAccessLevel) {
    return <Redirect to={accessLevelToRoute(authAccessLevel)} />;
  }

  return (
    <>
      <Spinner open={isBusy} />
      <Box display="flex" height="100%">
        <BackGroundImage height="100vh" display={['none', 'none', 'block']}>
          <WelcomeContainer>
            <WelcomeTitle fontSize="xxl" fontWeight="bld">{t('welcome_edx_inbox')}</WelcomeTitle>
            <WelcomeSubTitle fontSize="md" fontWeight="med">{t('customers_portal')}</WelcomeSubTitle>
          </WelcomeContainer>
        </BackGroundImage>
        <FormContainer centeredContent width={['100%', '100%', '25rem']} flexShrink="0">
          <Logo type="vertical" alt="loginLogo" width="130px" mb="40px" />
          <LoginLabel fontSize="md">{t('login')}</LoginLabel>
          <LoginForm />
        </FormContainer>
      </Box>
    </>
  );
};
