import * as actions from './actions';

const defaultState = {
  component: null,
};

const reducer = (state = defaultState, action = {}) => {
  switch (action.type) {
    case actions.SET_COMPONENT_TO_RENDER:
      return {
        ...state,
        component: action.payload,
      };
    case actions.CLEAR_COMPONENT_TO_RENDER:
      return {
        ...state,
        component: null,
      };
    default:
      return state;
  }
};

export default reducer;
