import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import { cfdiStates, paramsToFetchReports } from 'store/contants';
import {
  actions as docsActions,
  selectors as docsSelectors,
} from 'store/docs';
import { selectors as authSelectors } from 'store/auth';

import useSearch from 'services/search/useSearch';
import { allowedSearchParams } from 'services/search/helpers';
import Accordion from 'primitive-components/accordion';
import Page from 'components/page';
import DataBox from 'components/data-box';
import Filter from 'components/filter';
import TableBox, { tableTypes } from 'components/table-box';

import { CfdisPurchaseOrders } from 'components/reports';

const Home = () => {
  const dispatch = useDispatch();
  const { search, params, updateParams } = useSearch();
  const {
    page, rfcIssuer, rfcReceiver, dateEnd, dateIn,
  } = params;

  const [
    rfcsIssuer,
    rfcsReceiver,
    reports,
    cfdis,
    isBusy,
    totalPages,
  ] = useSelectors(
    authSelectors.getRFCsIssuer,
    authSelectors.getRFCsReceiver,
    docsSelectors.getReports,
    docsSelectors.getDocs,
    docsSelectors.getIsBusy,
    docsSelectors.getTotalPages,
  );
  const FetchDocs = () => dispatch(docsActions.FetchDocs({ ...params, ...paramsToFetchReports }));

  const handleChangePage = (pageT) => updateParams({ page: pageT });
  const handleChangeFilters = (filters) => updateParams(filters);

  useEffect(() => {
    FetchDocs();
  }, [search]);

  const filtersConfig = [
    {
      label: 'receiver_rfc',
      enableSearch: true,
      items: rfcsReceiver,
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_RECEIVER,
    },
    {
      label: 'issuing_rfc',
      enableSearch: true,
      items: rfcsIssuer,
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_ISSUER,
    },
    {
      label: 'state',
      enableSearch: true,
      items: Object.values(cfdiStates),
      type: 'dropdown',
      fieldName: allowedSearchParams.STATE,
    },
    {
      type: 'date',
    },
    {
      label: 'uuid_fiscal_folio',
      enableSearch: true,
      type: 'dropdown',
      fieldName: 'folio',
    },
  ];

  const headBtnsConfig = [
    { id: 'go_to_bills', design: 'shadow', customStyles: { rounded: true } },
  ];

  return (
    <Page title="welcome_to_edx_inbox">

      <CfdisPurchaseOrders isBusy={isBusy} reports={reports} />

      <DataBox
        title="cfdis"
        headBtnsConfig={headBtnsConfig}
        separator
      >
        <Accordion title="filter_search">
          <Filter
            config={filtersConfig}
            selected={{
              rfcIssuer,
              rfcReceiver,
              dateEnd,
              dateIn,
            }}
            onSubmit={handleChangeFilters}
          />
        </Accordion>

        <TableBox
          tableType={tableTypes.CFDI}
          elements={cfdis}
          noFoundMessage="no_bills_found"
          isBusy={isBusy}
          onChangePage={handleChangePage}
          totalPages={totalPages}
          page={page}
        />
      </DataBox>
    </Page>

  );
};

export default Home;
