import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import styled, { useTheme } from 'styled-components';
import PrimitiveSpinner from 'primitive-components/spinner';
import Box from 'primitive-components/box';
import Logo from 'components/logo';
import { useDialogState, Dialog } from 'reakit/Dialog';

const Container = styled(Box)`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items:center;
  justify-content: center;
`;

const StyledDialog = styled(Dialog)`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255,255,255,0.8);
  &:focus {
    outline: none;
  }
`;

const Spinner = ({ open }) => {
  const theme = useTheme();
  const dialog = useDialogState();
  const { hide, show } = dialog;

  useEffect(() => {
    (open ? show : hide)();
  }, [open]);

  return (
    <StyledDialog {...dialog} aria-label="Login" hideOnEsc={false} hideOnClickOutside={false}>
      <Container>
        <Logo type="vertical" alt="spinnerLogo" width="100px" marginBottom="15px" />
        <PrimitiveSpinner size="4rem" width="6px" color={theme.colors.inboxSpinner} />
      </Container>
    </StyledDialog>
  );
};

Spinner.defaultProps = {
  open: false,
};

Spinner.propTypes = {
  open: PropTypes.bool,
};

export default Spinner;
