import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import { getCollapsed } from 'store/layout/selectors';
import { Collapse } from 'store/layout/actions';
import ChangeUser from 'components/change-user-only-dev';

import BannerLogo from './BannerLogo';
import Profile from './Profile';

const StyledBannerContainer = styled(Box)`
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  background-color: white;
`;

const Banner = () => {
  const dispatch = useDispatch();
  const collapsed = useSelector(getCollapsed);

  return (
    <StyledBannerContainer>
      <BannerLogo
        closed={!collapsed}
        onClick={() => {
          dispatch(Collapse(!collapsed));
        }}
      />
      <ChangeUser />
      <Profile />
    </StyledBannerContainer>
  );
};

export default Banner;
