import React from 'react';
import { ToShortDate } from 'helpers/date';
import Action from '../actions/purchaseOrder';

export const heads = [
  'actions',
  'number',
  'issuing_rfc',
  'receiver_rfc',
  'total',
  'date',
  'state',
];

export const transmute = (data) => {
  const {
    estado, date, noOrdenC, rfcIssuer, rfcReceiver, total,
  } = data;
  const lastState = estado[estado.length - 1];

  return [
    <Action data={data} />,
    noOrdenC,
    rfcIssuer,
    rfcReceiver,
    total,
    ToShortDate(date, 'string'),
    lastState.estado,
  ];
};
