import {
  takeEvery, call, put, select,
} from 'redux-saga/effects';
import * as actions from './actions';

function* fetchRelDocs(api, { payload: params }) {
  try {
    yield put(actions.FetchRelDocsBusy());
    const { data } = yield call(api.fetchRelatedDocs, params);
    yield put(actions.FetchRelDocsSuccess(data));
  } catch (e) {
    yield put(actions.FetchRelDocsFailed());
  }
}

export default function* saga({ api }) {
  yield takeEvery(actions.FETCH_REL_DOCS, fetchRelDocs, api);
}
