import React from 'react';
import { useI18n } from 'services/i18n';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import { formatNumberToCurrency } from 'helpers/general';

const Card = styled(Box)`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: white;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 1.4rem;
  box-shadow:${({ theme }) => theme.shadows.lg};
`;

const CardHeader = styled(Box)`
color: #2c304d;
font-size: 2.6rem;
`;

const CardContent = styled(Box)`
color: #98a8b4;
font-size: 1.2rem;
`;

const TotalAmountInvoices = () => {
  const t = useI18n();
  const totalAmount = 3758435;
  return (
    <Card>
      <CardHeader>
        {formatNumberToCurrency(totalAmount)}
      </CardHeader>
      <CardContent>
        {t('total_amount_in_invoices')}
      </CardContent>
    </Card>
  );
};

export default TotalAmountInvoices;
