import React, { createContext, useContext } from 'react';
import { useDispatch } from 'react-redux';
import { selectors, actions } from 'store/modal';
import useSelectors from 'helpers/useSelectors';
import Modal from './Modal';

const ModalContext = createContext(null);

export const ModalProvider = ({ children }) => {
  const dispatch = useDispatch();
  const [
    hasComponent,
    Component,
  ] = useSelectors(
    selectors.getHasComponent,
    selectors.getComponent,
  );

  const onClose = () => dispatch(actions.ClearComponentToRender());
  const onOpenComponent = (component) => dispatch(actions.SetComponentToRender(component));

  return (
    <ModalContext.Provider value={{ onClose, onOpenComponent }}>
      {hasComponent && (
        <Modal open={hasComponent} onClose={onClose}>
          <Component />
        </Modal>
      )}
      {children}
    </ModalContext.Provider>
  );
};

export const useModal = () => useContext(ModalContext);
