import React from 'react';
import { useI18n } from 'services/i18n';
import { useDispatch } from 'react-redux';
import styled from 'styled-components';

import Box from 'primitive-components/box';
import PrimitiveButton from 'primitive-components/button';
import PrimitiveForm from 'primitive-components/form';
import Button from 'components/button';

import {
  LoginSubmit, ChangeUser, ChangeKeepSession,
} from 'store/auth/actions';

import {
  getAuthUser,
  getAuthKeepSession,
} from 'store/auth/selectors';
import useSelectors from 'helpers/useSelectors';

import Checkbox from './Checkbox';
import Input from './Input';

const ForgotPasswordButton = styled(PrimitiveButton)`
  margin-left: auto;
  color: ${({ theme }) => theme.colors.primaryButton};
  text-align: right;
`;

const Form = () => {
  const t = useI18n();
  const dispatch = useDispatch();
  const [
    user,
    keepSession,
  ] = useSelectors(
    getAuthUser,
    getAuthKeepSession,
  );

  const handleLoginSubmit = () => dispatch(LoginSubmit(user));
  const handleOnChange = (name, value) => dispatch(ChangeUser({ [name]: value }));
  const handleOnChangeKeepSession = (value) => dispatch(ChangeKeepSession(value));

  return (
    <PrimitiveForm width="100%" onSubmit={(e) => e.preventDefault()}>
      <Input
        id="email"
        type="email"
        placeholder={t('email')}
        value={user.email}
        onChange={(e) => handleOnChange('email', e.target.value)}
      />
      <Input
        id="password"
        type="password"
        placeholder={t('password')}
        value={user.password}
        onChange={(e) => handleOnChange('password', e.target.value)}
      />
      <Box display="flex">
        <Checkbox
          id="remember"
          label={t('remember_me')}
          checked={keepSession}
          onChange={handleOnChangeKeepSession}
        />
        <ForgotPasswordButton onClick={() => { alert('developing'); }}>
          {t('forgot_password')}
        </ForgotPasswordButton>
      </Box>
      <Box centeredContent>
        <Button
          design="primary"
          rounded
          p="0.75rem 2.5rem"
          mt="2rem"
          id="button_submit"
          type="submit"
          onClick={handleLoginSubmit}
        >
          {t('start_session')}
        </Button>
      </Box>
    </PrimitiveForm>
  );
};

export default Form;
