import React from 'react';
import { useDispatch } from 'react-redux';
import { push } from 'connected-react-router';
import Button from 'components/button';

import { PATH as pathProvider } from 'scenes/providers';
import { PATH as pathUser } from 'scenes/user';

import Box from 'primitive-components/box';
import Text from 'primitive-components/text';

const ChangeUser = () => {
  const dispatch = useDispatch();
  const handleChangeToUser = () => dispatch(push(pathUser));
  const handleChangeToProvider = () => dispatch(push(pathProvider));

  return (
    <Box display="flex" flexShrink="0" mr="2rem" alignItems="center" border="solid 1px rgb(150,150,150)" p="5px" borderRadius="8px">
      <Text whiteSpace="nowrap" >Only for development purpose</Text>
      <Button m="5px" design="primary" onClick={handleChangeToUser}>User</Button>
      <Button m="5px" design="default" onClick={handleChangeToProvider}>Provider</Button>
    </Box>

  );
};

export default ChangeUser;
