import React from 'react';
import PropTypes from 'prop-types';
import { useI18n } from 'services/i18n';
import styled, { useTheme } from 'styled-components';
import {
  typography, color, display, layout,
} from 'styled-system';
import { rgba } from 'polished';
import Box from 'primitive-components/box';

const Container = styled(Box)`
  width: 100%;
  height: inherit;
  max-height: inherit;
  overflow-x: auto;
`;

const Table = styled('table')`
  border-collapse: collapse;
  width: 100%;
  ${typography}
`;

const THead = styled('thead')`
  color:${({ theme }) => theme.colors.tableTextColor || '#000000'};
`;

const Th = styled('th')`
  padding: 0.5rem 0.7rem 1rem;
  text-align:left;
`;

const Td = styled('td')`
  width:1px;
  white-space:nowrap;
  padding: 12px;
  text-align:left;
  ${color}
`;

const Tr = styled('tr')`
  &:hover {
    background-color: ${({ theme }) => rgba(theme.colors.tableHead, 0.05) || '#FFFFFF'};
  }
  white-space: nowrap;
  ${color}
  ${display}
  ${layout}
`;

const Template = ({ heads, elements, elementTransmute }) => {
  const theme = useTheme();
  const t = useI18n();

  const trBackgroundColor = rgba(theme.colors.tableHead, 0.05) || '#FFFFFF';
  const tdColor = theme.colors.tableTextColor;

  return (
    <Container>
      <Table fontSize="sm">
        <THead>
          <Tr bg={trBackgroundColor}>
            {heads.map((head) => <Th key={head}>{t(head)}</Th>)}
          </Tr>
        </THead>
        <tbody>
          {elements && elements.map((elem, elemIndex) => (
            <Tr key={elemIndex}>
              {elementTransmute(elem).map((transElem, transElemIndex) => (
                <Td
                  key={transElemIndex}
                  color={transElemIndex === 1 ? tdColor : undefined}
                >
                  {transElem}
                </Td>
              ))}
            </Tr>
          ))}
        </tbody>
      </Table>
    </Container>
  );
};

Template.propTypes = {
  heads: PropTypes.arrayOf(PropTypes.string).isRequired,
  elements: PropTypes.arrayOf(PropTypes.any).isRequired,
  elementTransmute: PropTypes.func.isRequired,
};

export default Template;
