import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import DownloadPDFIcon from 'media/pdf.png';
import DownloadXMLIcon from 'media/xml.png';
import DownloadZIPIcon from 'media/zip.png';
import RelatedDocumentsIcon from 'media/file.png';
import SeeTrackingIcon from 'media/checklist.png';
import CheckSATStatusIcon from 'media/document.png';
import Trash from 'media/trash.png';
import Pencil from 'media/pencil.png';
import Refresh from 'media/refresh.png';

export const IconTypes = {
  DOWNLOAD_PDF: 'downloadPdf',
  DOWNLOAD_XML: 'downloadXml',
  DOWNLOAD_ZIP: 'downloadZip',
  SEE_TRACKING: 'seeTracking',
  CHECK_SAT: 'checkSat',
  RELATED_DOCS: 'relatedDocs',
  DELETE: 'delete',
  EDIT: 'edit',
  REFRESH: 'refresh',
};

const StyledImg = styled('img')`
  width: 1.5rem;
  cursor: pointer;
`;

const typeToIcon = {
  [IconTypes.DOWNLOAD_PDF]: DownloadPDFIcon,
  [IconTypes.DOWNLOAD_XML]: DownloadXMLIcon,
  [IconTypes.DOWNLOAD_ZIP]: DownloadZIPIcon,
  [IconTypes.SEE_TRACKING]: SeeTrackingIcon,
  [IconTypes.CHECK_SAT]: CheckSATStatusIcon,
  [IconTypes.RELATED_DOCS]: RelatedDocumentsIcon,
  [IconTypes.DELETE]: Trash,
  [IconTypes.EDIT]: Pencil,
  [IconTypes.REFRESH]: Refresh,
};

const Icon = ({ type, onClick, href }) => {
  const icon = typeToIcon[type];
  const arg = href || onClick || '#';
  return (
    typeof arg === 'string' ? (
      <a href={arg}>
        <StyledImg src={icon} alt={type} />
      </a>
    ) : (
      <StyledImg src={icon} alt={type} onClick={arg} />
    )
  );
};

Icon.defaultProps = {
  type: IconTypes.DOWNLOAD_PDF,
};

Icon.propTypes = {
  type: PropTypes.oneOf(Object.values(IconTypes)),
  onClick: PropTypes.func,
  href: PropTypes.string,
};

// /////////////////////////////////////////////////////////////////
const Template = ({ icons }) => (
  <Box display="flex">
    {icons?.map((icon, index) => <Icon key={index} {...icon} />)}
  </Box>
);

Template.propTypes = {
  icons: PropTypes.arrayOf(
    PropTypes.shape(Icon.propTypes),
  ),
};

export default Template;
