import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import useSearch from 'services/search/useSearch';

import {
  actions as docsActions,
  selectors as docsSelectors,
} from 'store/docs';

import { selectors as authSelectors } from 'store/auth';

import DataBox from 'components/data-box';
import Accordion from 'primitive-components/accordion';
import Filter from 'components/filter';
import TableBox from 'components/table-box';

import { allowedSearchParams } from 'services/search/helpers';
import { cfdiStates } from 'store/contants';
import { roles } from './constants';

const role = roles[0];

const CFDISTable = () => {
  const dispatch = useDispatch();
  const { search: { [role]: search = '' }, params: { [role]: params = {} }, updateParams: updateParamsT } = useSearch();
  const updateParams = (p) => updateParamsT(p, role);

  const {
    page, rfcIssuer, rfcReceiver, dateEnd, dateIn,
  } = params;

  const [
    rfcsIssuer,
    rfcsReceiver,
    cfdis,
    isBusy,
    totalPages,
  ] = useSelectors(
    authSelectors.getRFCsIssuer,
    authSelectors.getRFCsReceiver,
    docsSelectors.getDocs,
    docsSelectors.getIsBusy,
    docsSelectors.getTotalPages,
  );

  const FetchDocs = () => dispatch(docsActions.FetchDocs(params));

  const handleChangePage = (pageT) => updateParams({ page: pageT });
  const handleChangeFilters = (filters) => updateParams(filters);

  useEffect(() => {
    FetchDocs();
  }, [search]);

  const filtersConfig = [
    {
      label: 'receiver_rfc',
      enableSearch: true,
      items: rfcsReceiver,
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_RECEIVER,
    },
    {
      label: 'issuing_rfc',
      enableSearch: true,
      items: rfcsIssuer,
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_ISSUER,
    },
    {
      label: 'state',
      enableSearch: true,
      items: Object.values(cfdiStates),
      type: 'dropdown',
      fieldName: allowedSearchParams.STATE,
    },
    {
      type: 'date',
    },
    {
      label: 'uuid_fiscal_folio',
      enableSearch: true,
      type: 'dropdown',
      fieldName: 'folio',
    },
  ];

  return (
    <DataBox
      title="cfdis"
      separator
    >
      <Accordion title="filter_search">
        <Filter
          config={filtersConfig}
          selected={{
            rfcIssuer,
            rfcReceiver,
            dateEnd,
            dateIn,
          }}
          onSubmit={handleChangeFilters}
        />
      </Accordion>
      <TableBox
        tableType="cfdi"
        elements={cfdis}
        noFoundMessage="no_bills_found"
        isBusy={isBusy}
        onChangePage={handleChangePage}
        totalPages={totalPages}
        page={page}
      />
    </DataBox>
  );
};

export default CFDISTable;
