import React from 'react';
import { ToShortDate } from 'helpers/date';
import Button from 'components/button';
import { formatNumberToCurrency } from 'helpers/general';
import { cfdiStates } from 'store/contants';
import Action from '../actions/cfdi';

const ButtonError = () => <Button p="3.5px 7px" rounded fontSize="xs" design="error">Con error</Button>;

export const heads = [
  'actions',
  'serie',
  'folio',
  'date',
  'issuing_rfc',
  'receiver_rfc',
  'state',
  'total',
  'uuid_fiscal_folio',
];

export const transmute = (data) => {
  const {
    estado, uuid, serie, folio, date, rfcIssuer, rfcReceiver, total,
  } = data;
  const lastState = estado[estado.length - 1];

  return [
    <Action data={data} />,
    serie,
    folio,
    ToShortDate(date, 'string'),
    rfcIssuer,
    rfcReceiver,
    lastState.estado === cfdiStates.ERROR ? <ButtonError /> : lastState.estado,
    formatNumberToCurrency(total),
    uuid,
  ];
};
