import { NAME } from './constants';

export const FETCH_REL_DOCS = `${NAME}.FETCH_REL_DOCS`;
export const FetchRelDocs = (data) => ({
  type: FETCH_REL_DOCS,
  payload: data,
});

export const FETCH_REL_DOCS_BUSY = `${NAME}.FETCH_REL_DOCS_BUSY`;
export const FetchRelDocsBusy = () => ({
  type: FETCH_REL_DOCS_BUSY,
  payload: null,
});

export const FETCH_REL_DOCS_FAILED = `${NAME}.FETCH_REL_DOCS_FAILED`;
export const FetchRelDocsFailed = () => ({
  type: FETCH_REL_DOCS_FAILED,
  payload: null,
});

export const FETCH_REL_DOCS_SUCCESS = `${NAME}.FETCH_REL_DOCS_SUCCESS`;
export const FetchRelDocsSuccess = (data) => ({
  type: FETCH_REL_DOCS_SUCCESS,
  payload: data,
});
