import RequestStatus from 'helpers/requestStatus';

import * as actions from './actions';
import { NAME } from './constants';

const name = 'requestStatus';
const actionsRequestStatus = {
  NOT_STARTED: [
    actions.FETCH_DOCS_SUCCESS,
  ],
  BUSY: [
    actions.FETCH_DOCS_BUSY,
  ],
  SUCCEEDED: [],
  FAILED: [
    actions.FETCH_DOCS_FAILED,
  ],
};

const {
  reducer,
  defaultStatus,
  getIsNotStarted,
  getIsBusy,
  getIsSuccess,
  getIsFailed,
} = RequestStatus({
  nestedPath: [NAME],
  storeName: name,
  actions: actionsRequestStatus,
});

export {
  reducer,
  name,
  defaultStatus,

  getIsNotStarted,
  getIsBusy,
  getIsSuccess,
  getIsFailed,
};
