import React from 'react';
import { userManagementPropType } from 'helpers/propTypes';
import Template, { IconTypes } from './Template';

const Actions = ({ data = {} }) => {
  const iconsData = [
    { type: IconTypes.EDIT },
    { type: IconTypes.REFRESH },
    { type: IconTypes.DELETE },
  ];

  return (
    <Template icons={iconsData} />
  );
};

Actions.propTypes = {
  data: userManagementPropType,
};

export default Actions;
