import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import Banner from 'components/banner';
import Footer from 'components/footer';
import ScrollArrow from 'components/scroll-top';
import { useSelector } from 'react-redux';
import { getCollapsed } from 'store/layout/selectors';

const sideBarWidth = '17rem';
const sideBarWidthXs = '90px';
const bannerHeight = '80px';
const paddingContent = '1.5rem';

const BannerContainer = styled(Box)`
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  height: ${() => bannerHeight};
`;
const SideBarContainer = styled(Box)`
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  margin-top: ${() => bannerHeight};
  width: ${() => sideBarWidth};
`;

const ContentContainer = styled(Box)`
  margin-top: ${() => bannerHeight};
  padding: ${() => paddingContent};
  padding-left: ${(props) => `calc(${
    props.collapsed ? sideBarWidth : sideBarWidthXs
  } + ${paddingContent})`};
  background-color: rgb(242, 243, 248);
`;

const FooterContainer = styled(Box)`
  position: absolute;
  bottom: -80px;
  width:100vw; 
  background-color: #ffffff;  
  padding-left: ${(props) => `calc(${props.collapsed ? sideBarWidth : sideBarWidthXs})`}; 
`;

const Layout = ({ sideBar: SideBar, content: Content }) => {
  const collapsed = useSelector(getCollapsed);

  return (
    <>
      <ContentContainer collapsed={collapsed}>
        <Content />
      </ContentContainer>
      <FooterContainer collapsed={collapsed}>
        <Footer />
      </FooterContainer>
      <BannerContainer>
        <Banner />
      </BannerContainer>
      <SideBarContainer>
        <SideBar />
      </SideBarContainer>
      <ScrollArrow />
    </>
  );
};
Layout.propTypes = {
  sideBar: PropTypes.func,
  content: PropTypes.func,
};

export default Layout;
