import React from 'react';
import {
  SvgBuildingSolid,
  SvgChartBarSolid,
  SvgHomeSolid,
  SvgShoppingCartSolid,
  SvgUserFriendsSolid,
  SvgFileAltSolid,
} from 'react-line-awesome-svg';

import SideBar from 'components/side-bar';

import { PATH as pathHome } from './home';
import { PATH as pathReports } from './reports';
import { PATH as pathCFDIs } from './cfdis';
import { PATH as pathPurchaseOrders } from './purchase-orders';
import { PATH as pathProviders } from './providers';
import { PATH as pathUserRegistration } from './users-registration';

const items = [
  { id: 'home', route: pathHome, icon: SvgHomeSolid },
  { id: 'reports', route: pathReports, icon: SvgChartBarSolid },
  {
    id: 'purchase_orders',
    route: pathPurchaseOrders,
    icon: SvgShoppingCartSolid,
  },
  { id: 'cfdis', route: pathCFDIs, icon: SvgFileAltSolid },
  { id: 'providers', route: pathProviders, icon: SvgBuildingSolid },
  {
    id: 'users_registration',
    route: pathUserRegistration,
    icon: SvgUserFriendsSolid,
  },
];

export default () => <SideBar items={items} />;
