import { NAME } from './constants';

export const SET_COMPONENT_TO_RENDER = `${NAME}.SET_COMPONENT_TO_RENDER`;
export const SetComponentToRender = (component) => ({
  type: SET_COMPONENT_TO_RENDER,
  payload: component,
});

export const CLEAR_COMPONENT_TO_RENDER = `${NAME}.CLEAR_COMPONENT_TO_RENDER`;
export const ClearComponentToRender = () => ({
  type: CLEAR_COMPONENT_TO_RENDER,
  payload: null,
});
