import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Checkbox } from 'reakit';
import styled, { useTheme } from 'styled-components';
import { styledSystem } from 'services/theme/helpers';

import Text from 'primitive-components/text';
import { rgba } from 'polished';

const StyledContainer = styled('label')`
    display: flex;
    align-items: center;
    width: fit-content;
    ${styledSystem};
`;

const StyledCheckbox = styled(Checkbox)`
    appearance: none;
    background-color: ${({ backgroundcolor }) => backgroundcolor || '#FFFFFF'};
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    width: ${({ size }) => size};
    height: ${({ size }) => size};
    border: ${({ bordercolor }) => (bordercolor ? `solid 1px ${bordercolor}` : 'none')};
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    &:after {
        content: "✓";
        position: absolute;
        opacity: 0;
        transition: all 0.2s ease-in-out;
        color: black;
        font-size: 1.2rem;
    }
    &:checked {
        &:after {
          opacity: 1;
        }
    }
    ${styledSystem}
`;

const Component = (props) => {
  const {
    label, checked: checkedIn, onChange, labelFontSize, labelColor, backgroundColor, borderColor, size, ...restProps
  } = props;

  const [checked, setChecked] = useState(false);
  const theme = useTheme();

  useEffect(() => {
    setChecked(checkedIn);
  }, [checkedIn]);

  const handleOnChange = ({ target: { checked: checkedChange } }) => {
    setChecked(checkedChange);
    onChange?.(checkedChange);
  };

  return (
    <StyledContainer {...restProps}>
      <StyledCheckbox
        backgroundcolor={backgroundColor || rgba(theme.colors.styledForm, 0.1)}
        bordercolor={borderColor || 'transparent'}
        size={size || '1.3rem'}
        checked={checked}
        onChange={handleOnChange}
      />
      <Text
        marginLeft="1rem"
        fontSize={labelFontSize || 'sm'}
        color={labelColor || theme.colors.placeholderText}
      >
        {label}
      </Text>
    </StyledContainer>
  );
};

Component.defaultProps = {
};

Component.propTypes = {
  label: PropTypes.string,
  labelFontSize: PropTypes.string,
  labelColor: PropTypes.string,
  backgroundColor: PropTypes.string,
  borderColor: PropTypes.string,
  size: PropTypes.string,
  checked: PropTypes.bool,
  onChange: PropTypes.func,
};

export default Component;
