import { NAME } from './constants';

export const LOGIN_SUBMIT = `${NAME}.LOGIN_SUBMIT`;
export const LoginSubmit = (user) => ({
  type: LOGIN_SUBMIT,
  payload: user,
});

export const LOGIN_FAILED = `${NAME}.LOGIN_FAILED`;
export const LoginFailed = (error) => ({
  type: LOGIN_FAILED,
  payload: error,
});

export const LOGIN_SUCCESS = `${NAME}.LOGIN_SUCCESS`;
export const LoginSuccess = (authData) => ({
  type: LOGIN_SUCCESS,
  payload: authData,
});

export const LOGIN_BUSY = `${NAME}.LOGIN_BUSY`;
export const LoginBusy = () => ({
  type: LOGIN_BUSY,
  payload: null,
});

export const CHANGE_USER = `${NAME}.CHANGE_USER`;
export const ChangeUser = (change) => ({
  type: CHANGE_USER,
  payload: change,
});

export const LOGOUT = `${NAME}.LOGOUT`;
export const Logout = () => ({
  type: LOGOUT,
  payload: null,
});

export const REGISTER_SUBMIT = `${NAME}.REGISTER_SUBMIT`;
export const RegisterSubmit = (user) => ({
  type: REGISTER_SUBMIT,
  payload: user,
});

export const REGISTER_FAILED = `${NAME}.REGISTER_FAILED`;
export const RegisterFailed = (error) => ({
  type: REGISTER_FAILED,
  payload: error,
});

export const REGISTER_SUCCESS = `${NAME}.REGISTER_SUCCESS`;
export const RegisterSuccess = () => ({
  type: REGISTER_SUCCESS,
  payload: null,
});

export const REGISTER_BUSY = `${NAME}.REGISTER_BUSY`;
export const RegisterBusy = () => ({
  type: REGISTER_BUSY,
  payload: null,
});

export const CHANGE_KEEP_SESSION = `${NAME}.CHANGE_KEEP_SESSION`;
export const ChangeKeepSession = (value) => ({
  type: CHANGE_KEEP_SESSION,
  payload: value,
});

export const LOAD_SESSION_LS_SUCCESS = `${NAME}.LOAD_SESSION_LS_SUCCESS`;
export const LoadSessionLsSuccess = (value) => ({
  type: LOAD_SESSION_LS_SUCCESS,
  payload: value,
});
