import React from 'react';
import PropTypes from 'prop-types';
import Box from 'primitive-components/box';
import Spinner from 'primitive-components/spinner';
import Pagination from 'components/pagination';
import { useI18n } from 'services/i18n';
import Template from './Template';

import {
  heads as headsPurchaseOrders,
  transmute as transmutePurchaseOrders,
} from './transmuters/purchaseOrders';

import {
  heads as headsCFDI,
  transmute as transmuteCFDI,
} from './transmuters/cfdi';

import {
  heads as headsRelatedDocs,
  transmute as transmuteRelatedDocs,
} from './transmuters/relatedDocs';

import {
  heads as headsAreaManagement,
  transmute as transmuteAreaManagement,
} from './transmuters/areaManagement';

import {
  heads as headsBill,
  transmute as transmuteBill,
} from './transmuters/bills';

import {
  heads as headsProfileManagement,
  transmute as transmuteProfileManagement,
} from './transmuters/profileManagement';

import {
  heads as headsUserManagement,
  transmute as transmuteUserManagement,
} from './transmuters/userManagement';

import {
  heads as headsProviderManagement,
  transmute as transmuteProviderManagement,
} from './transmuters/providerManagement';

export const tableTypes = {
  PURCHASE_ORDERS: 'purchaseOrders',
  BILL: 'bill',
  CFDI: 'cfdi',
  RELATED_DOCS: 'relatedDocs',
  AREA_MANAGEMENT: 'areaManagement',
  PROFILE_MANAGEMENT: 'profileManagement',
  USER_MANAGEMENT: 'userManagement',
  PROVIDER_MANAGEMENT: 'providerManagement',
};

const AllHeads = {
  [tableTypes.PURCHASE_ORDERS]: headsPurchaseOrders,
  [tableTypes.BILL]: headsBill,
  [tableTypes.CFDI]: headsCFDI,
  [tableTypes.RELATED_DOCS]: headsRelatedDocs,
  [tableTypes.AREA_MANAGEMENT]: headsAreaManagement,
  [tableTypes.PROFILE_MANAGEMENT]: headsProfileManagement,
  [tableTypes.USER_MANAGEMENT]: headsUserManagement,
  [tableTypes.PROVIDER_MANAGEMENT]: headsProviderManagement,
};

const AllTransmute = {
  [tableTypes.PURCHASE_ORDERS]: transmutePurchaseOrders,
  [tableTypes.BILL]: transmuteBill,
  [tableTypes.CFDI]: transmuteCFDI,
  [tableTypes.RELATED_DOCS]: transmuteRelatedDocs,
  [tableTypes.AREA_MANAGEMENT]: transmuteAreaManagement,
  [tableTypes.PROFILE_MANAGEMENT]: transmuteProfileManagement,
  [tableTypes.USER_MANAGEMENT]: transmuteUserManagement,
  [tableTypes.PROVIDER_MANAGEMENT]: transmuteProviderManagement,
};

export const tableSizes = {
  XS: 'xs',
  SM: 'sm',
  MD: 'md',
  LG: 'lg',
  XL: 'xl',
};

export const sizeToDim = {
  [tableSizes.XS]: '10rem',
  [tableSizes.SM]: '15rem',
  [tableSizes.MD]: '25rem',
  [tableSizes.LG]: '40rem',
  [tableSizes.XL]: '60rem',
};

const TableBox = ({
  tableType,
  elements,

  isBusy,
  noFoundMessage,

  onChangePage,
  totalPages,
  page,
  tableSize = tableSizes.MD,
}) => {
  const t = useI18n();
  const hasElements = !!elements?.length;

  const heads = AllHeads[tableType];
  const transmute = AllTransmute[tableType];
  const height = sizeToDim[tableSize];

  const Render = () => (hasElements ? (
    <Template
      heads={heads}
      elements={elements}
      elementTransmute={transmute}
    />
  ) : t(noFoundMessage));

  const hasPagination = !!(page && totalPages) && hasElements;

  return (
    <>
      <Box centeredContent height={height} border="solid 1px rgb(240,240,240)">
        {isBusy
          ? <Spinner />
          : <Render />}
      </Box>

      {hasPagination && (
        <Pagination
          onChange={onChangePage}
          totalPages={totalPages}
          page={page}
          ml="auto"
        />
      )}
    </>
  );
};

TableBox.propTypes = {
  tableType: PropTypes.oneOf(Object.values(tableTypes)).isRequired,
  elements: PropTypes.arrayOf(PropTypes.any),

  isBusy: PropTypes.bool,
  noFoundMessage: PropTypes.string,

  onChangePage: PropTypes.func,
  totalPages: PropTypes.number,
  page: PropTypes.number,
  tableSize: PropTypes.oneOf(Object.values(tableSizes)),
};
export default TableBox;
