import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { margin } from 'styled-system';
import SimpleLogo from 'media/simple-inbox-logo.png';
import HLogo from 'media/h-inbox-logo.png';
import VLogo from 'media/v-inbox-logo.png';

const StyledLogo = styled('img')`
    ${margin};
`;

const logoSrc = {
  simple: SimpleLogo,
  vertical: VLogo,
  horizontal: HLogo,
};

const Logo = ({ type, alt, ...remainderProps }) => <StyledLogo src={logoSrc[type]} alt={alt} {...remainderProps} />;

Logo.defaultProps = {
  type: 'simple',
  alt: 'simpleLogo',
};

Logo.propTypes = {
  type: PropTypes.oneOf(['simple', 'vertical', 'horizontal']),
  alt: PropTypes.string,
};

export default Logo;
