import React, { useEffect, useState } from 'react';
import Icon from 'primitive-components/icon';
import styled from 'styled-components';
import {
  SvgArrowUpSolid,
} from 'react-line-awesome-svg';
import Box from 'primitive-components/box';

const ArrowWrapper = styled.div`
  .scrollTop {
    position: fixed;
    width: 100%;
    bottom: 20px;
    align-items: center;
    height: 20px;
    justify-content: center;
    z-index: 1000;
    cursor: pointer;
    animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.5;
    background: #fff;
    height: 41px;
    width: 41px;
    border-radius: 50%;
    right: 1rem;
    bottom: 3rem;
    box-shadow:${({ theme }) => theme.shadows.lg};
    z-index: 10000;
  }

  .scrollTop:hover {
    opacity: 1;
  }

  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
`;

const ScrollArrow = () => {
  const [showScroll, setShowScroll] = useState(false);

  const checkScrollTop = () => {
    if (window.pageYOffset > 400) {
      setShowScroll(true);
    } else if (window.pageYOffset <= 400) {
      setShowScroll(false);
    }
  };

  const scrollTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  useEffect(() => {
    window.addEventListener('scroll', checkScrollTop);
    return () => {
      window.removeEventListener('scroll', checkScrollTop);
    };
  }, []);

  return (
    <ArrowWrapper>
      <Box
        className="scrollTop"
        display={showScroll ? 'flex' : 'none'}
      >
        <Icon onClick={scrollTop} size={20} icon={SvgArrowUpSolid} />
      </Box>
    </ArrowWrapper>
  );
};

export default ScrollArrow;
