import React from 'react';
import Action from '../actions/profileManagement';

export const heads = [
  'actions',
  'profile_name',
  'amount_of_users',
  'amount_of_rules',
];

export const transmute = (data) => {
  const {
    profileName,
    amountOfUsers,
    amountOfRules,
  } = data;

  return [
    <Action data={data} />,
    profileName,
    amountOfUsers,
    amountOfRules,
  ];
};
