import React from 'react';
import Action from '../actions/userManagement';

export const heads = [
  'actions',
  'number',
  'name',
  'email_title',
  'profile',
  'area',
  'status',
];

export const transmute = (data) => {
  const {
    number, name, email, profile, area, status,
  } = data;

  return [
    <Action data={data} />,
    number, name, email, profile, area, status,
  ];
};
