import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { useI18n } from 'services/i18n';
import Box from 'primitive-components/box';
import Button from 'components/button';
import Separator from 'primitive-components/separator';
import MaterialModal from '@material-ui/core/Modal';

const Container = styled(Box)`
  background-color: #FFFFFF;
`;

const Modal = ({ children, onClose, ...remainderProps }) => {
  const t = useI18n();
  return (
    <MaterialModal {...remainderProps}>
      <Box centeredContent height="100%">
        <Container>
          {children}
          <Separator />
          <Box display="flex" alignItems="center">
            <Button rounded m="10px 10px 10px auto" p="12px 30px" onClick={onClose}>{t('close')}</Button>
          </Box>
        </Container>
      </Box>
    </MaterialModal>
  );
};

Modal.propTypes = {
  children: PropTypes.node.isRequired,
  onClose: PropTypes.func,
};

export default Modal;
