import { NAME } from './constants';

export const FETCH_PCS = `${NAME}.FETCH_PCS`;
export const FetchPCs = (data) => ({
  type: FETCH_PCS,
  payload: data,
});

export const FETCH_PCS_BUSY = `${NAME}.FETCH_PCS_BUSY`;
export const FetchPCsBusy = () => ({
  type: FETCH_PCS_BUSY,
  payload: null,
});

export const FETCH_PCS_FAILED = `${NAME}.FETCH_PCS_FAILED`;
export const FetchPCsFailed = () => ({
  type: FETCH_PCS_FAILED,
  payload: null,
});

export const FETCH_PCS_SUCCESS = `${NAME}.FETCH_PCS_SUCCESS`;
export const FetchPCsSuccess = (data) => ({
  type: FETCH_PCS_SUCCESS,
  payload: data,
});
