import { createSelector } from 'reselect';

const Statuses = {
  NOT_STARTED: 'NOT_STARTED',
  BUSY: 'BUSY',
  SUCCEEDED: 'SUCCEEDED',
  FAILED: 'FAILED',
};
const defaultStatus = Statuses.NOT_STARTED;

const defaultActions = {
  NOT_STARTED: [],
  BUSY: [],
  SUCCEEDED: [],
  FAILED: [],
};

const existActionType = (actionsTypes, action) => actionsTypes.some((type) => action.type === type);
const pathReducer = (obj, key) => ((obj && obj[key] !== 'undefined') ? obj[key] : undefined);
const getNestedObject = (nestedObj, pathArr) => pathArr.reduce(pathReducer, nestedObj);

export default ({
  nestedPath = [],
  storeName = 'REQUEST_STATUS',
  actions = defaultActions,
}) => {
  const getStatus = (state) => getNestedObject(state, [...nestedPath, storeName]);

  const getIsNotStarted = createSelector(getStatus, (status) => status === Statuses.NOT_STARTED);
  const getIsBusy = createSelector(getStatus, (status) => status === Statuses.BUSY);
  const getIsSuccess = createSelector(getStatus, (status) => status === Statuses.SUCCEEDED);
  const getIsFailed = createSelector(getStatus, (status) => status === Statuses.FAILED);

  const keys = Object.keys(actions);

  const reducer = (state = defaultStatus, action = {}) => {
    let newState = state;
    keys.forEach((key) => {
      if (existActionType(actions[key], action)) newState = Statuses[key];
    });
    return newState;
  };

  return {
    reducer, defaultStatus, getIsNotStarted, getIsBusy, getIsSuccess, getIsFailed,
  };
};
