import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import styled from 'styled-components';
import { useI18n } from 'services/i18n';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import Separator from 'primitive-components/separator';
import Avatar from '@material-ui/core/Avatar';
import Popover from '@material-ui/core/Popover';
import { actions as authActions } from 'store/auth';

const ContentContainer = styled(Box)`
    display: flex;
    flex-direction: column;
    box-shadow:${({ theme }) => theme.shadows.xl};
    background-color: white;
    padding:1rem 0;
`;
const StyledItem = styled(Text)`
    cursor: pointer;
    white-space: nowrap;
    margin:0.3rem 1rem;
    color:${({ theme }) => theme.colors.profileItemText || '#000000'};
`;

const Content = (props) => {
  const dispatch = useDispatch();
  const t = useI18n();
  const items = [
    { id: 'my_profile', onClick: () => { alert('My profile'); } },
    { id: 'my_subscription', onClick: () => { alert('My subscription'); } },
    { id: 'logout', separator: true, onClick: () => dispatch(authActions.Logout()) },

  ];
  return (
    <ContentContainer {...props}>
      <Box p="0.5rem" centeredContent><Avatar>RC</Avatar></Box>
      {items.map((item) => (
        <div key={item.id}>
          {item.separator && <Separator margin="1rem 0" />}
          <StyledItem id={item.id} onClick={item.onClick} fontSize="xs" fontWeight="bld">
            {t(item.id)}
          </StyledItem>
        </div>
      ))}
    </ContentContainer>
  );
};

const Profile = () => {
  const [anchorEl, setAnchorEl] = useState(null);
  const handleOpen = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  return (
    <>
      <Avatar
        aria-controls="profile-popover"
        onClick={handleOpen}
      >
        RC
      </Avatar>

      <Popover
        id="profile-popover"
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <Content />
      </Popover>
    </>
  );
};

export default Profile;
