import RequestStatus from 'helpers/requestStatus';

import * as actions from './actions';
import { NAME } from './constants';

const name = 'requestStatus';
const actionsRequestStatus = {
  NOT_STARTED: [
    actions.LOGIN_SUCCESS,
    actions.REGISTER_SUCCESS,
  ],
  BUSY: [
    actions.LOGIN_BUSY,
    actions.REGISTER_BUSY,
  ],
  SUCCEEDED: [
    actions.LOGIN_SUCCESS,
    actions.REGISTER_SUCCESS,
  ],
  FAILED: [
    actions.LOGIN_FAILED,
    actions.REGISTER_FAILED,
  ],
};

const {
  reducer,
  defaultStatus,
  getIsNotStarted: getIsAuthNotStarted,
  getIsBusy: getIsAuthBusy,
  getIsSuccess: getIsAuthSuccess,
  getIsFailed: getIsAuthFailed,
} = RequestStatus({
  nestedPath: [NAME],
  storeName: name,
  actions: actionsRequestStatus,
});

export {
  reducer,
  name,
  defaultStatus,
  getIsAuthNotStarted,
  getIsAuthBusy,
  getIsAuthSuccess,
  getIsAuthFailed,
};
