import React from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import Grid from '@material-ui/core/Grid';
import DataBox from 'components/data-box';
import { useI18n } from 'services/i18n';
import { useTheme } from 'styled-components';
import { Chart } from 'components/chart';
import Box from 'primitive-components/box';
import Spinner from 'primitive-components/spinner';
import { NumberCard } from 'components/data-card';
import { splitCommaSeparatedArray } from 'helpers/general';
import { reportsPropType } from 'helpers/propTypes';

export const transmute = ({ reports, t, theme }) => {
  const months = splitCommaSeparatedArray(
    t('short_months_comma_separate'),
  );

  return {
    type: 'bar',
    colors: [
      '#97bf3d',
      '#cd4927',
      '#e4e8f0',
      '#a49ab8',
    ],
    data: [
      {
        title: t('cfdis_received'),
        backgroundColor: '#97bf3d', // theme.colors.chartSuccess,
        data: [1254, 2563, 25487, 48, 525, 487, 2541, 253, 25, 23, 56, 1],
        labels: months,
      },
      {
        title: t('cfdis_with_error'),
        backgroundColor: '#cd4927', // theme.colors.chartInfo1,
        data: [1254, 2563, 25487, 48, 525, 487, 2541, 253, 25, 23, 56, 1],
        labels: months,
      },
      {
        title: t('completed_oc'),
        backgroundColor: '#cd4927', // theme.colors.chartInfo1,
        data: [1254, 2563, 25487, 48, 525, 487, 2541, 253, 25, 23, 56, 1],
        labels: months,
      },
      {
        title: t('not_attended_oc'),
        backgroundColor: '#e4e8f0', // theme.colors.chartError,
        data: [1254, 2563, 25487, 48, 525, 487, 2541, 253, 25, 23, 56, 1],
        labels: months,
      },
      {
        title: t('partial_attended_oc'),
        backgroundColor: '#e4e8f0', // theme.colors.chartError,
        data: [1254, 2563, 25487, 48, 525, 487, 2541, 253, 25, 23, 56, 1],
        labels: months,
      },
    ],
  };
};

const Totals = ({ reports }) => {
  const theme = useTheme();
  const t = useI18n();

  if (_.isEmpty(reports)) return null;

  const { cfdis, ordeDeCompra } = reports;
  const cardsData = [
    { number: cfdis?.cfdis.total, label: 'cfdis_received' },
    { number: cfdis?.cfdiError.total, label: 'cfdis_with_error' },
    { number: ordeDeCompra?.ocAtendida.total, label: 'completed_oc' },
    { number: ordeDeCompra?.ocNoAtendida.total, label: 'not_attended_oc' },
    { number: ordeDeCompra?.ocAtendidaParcial.total, label: 'partial_attended_oc' },
  ];
  return (
    <Box>
      {cardsData.map((data) => (
        <NumberCard
          key={data.label}
          number={data.number}
          label={t(data.label)}
          primaryTextColor={theme.colors.dataCardDefaultTitleColor}
          secondaryTextColor={theme.colors.primaryText}
        />
      ))}
    </Box>
  );
};

Totals.propTypes = {
  reports: reportsPropType,
};

const CfdisPurchaseOrders = ({ reports, isBusy, ...remainderProps }) => {
  const theme = useTheme();
  const t = useI18n();

  const chartConfig = transmute({
    reports,
    t,
    theme,
  });

  return (
    <DataBox title="cfdis_orders_purchase" {...remainderProps}>
      <Grid container>
        <Grid item xs={9}>
          <Chart {...chartConfig} />
        </Grid>
        <Grid item xs={3}>
          <Box centeredContent height="90%">
            {isBusy ? <Spinner /> : <Totals reports={reports} />}
          </Box>
        </Grid>
      </Grid>
    </DataBox>
  );
};

CfdisPurchaseOrders.propTypes = {
  isBusy: PropTypes.bool,
  reports: reportsPropType,
};

export default CfdisPurchaseOrders;
