import { createSelector } from 'reselect';
import { NAME } from './constants';

import {
  getIsNotStarted,
  getIsBusy,
  getIsSuccess,
  getIsFailed,
} from './status';

const getFetchedData = (state) => state[NAME].data || {};

const getRelDocs = createSelector(
  getFetchedData,
  (data) => data?.list || [],
);

const getTotalPages = createSelector(
  getFetchedData,
  (data) => data?.pages,
);

export {
  getRelDocs,
  getTotalPages,

  getIsNotStarted,
  getIsBusy,
  getIsSuccess,
  getIsFailed,
};
