import React, { useEffect } from 'react';
import DataBox from 'components/data-box';
import Filter from 'components/filter';
import useSearch from 'services/search/useSearch';
import { useDispatch } from 'react-redux';
import { allowedSearchParams } from 'services/search/helpers';
import TableBox, { tableTypes } from 'components/table-box';
import { SvgPlusSolid } from 'react-line-awesome-svg';
import {
  actions as docsActions,
  selectors as docsSelectors,
} from 'store/docs';

const ProfileManagement = () => {
  const dispatch = useDispatch();
  const { search, params, updateParams } = useSearch();
  const {
    area, rfc,
  } = params;

  const headBtnsConfig = [
    { id: 'new', design: 'primary', svgIcon: SvgPlusSolid },
  ];

  const profiles = [{
    profileName: 'Administrador',
    amountOfUsers: 35,
    amountOfRules: 15,
  },
  {
    profileName: 'Editor',
    amountOfUsers: 22,
    amountOfRules: 30,
  }];

  const filtersConfig = [
    {
      label: 'profile_name',
      enableSearch: true,
      items: [], // TODO: Poner aqui los items desde el back
      type: 'dropdown',
      fieldName: allowedSearchParams.RFC_RECEIVER,
    },
  ];

  const handleChangeFilters = (filters) => updateParams(filters);

  return (
    <DataBox title="profile_management" separator headBtnsConfig={headBtnsConfig}>
      <Filter
        config={filtersConfig}
        selected={{
          area,
          rfc,
        }}
        onSubmit={handleChangeFilters}
      />

      <TableBox
        tableType={tableTypes.PROFILE_MANAGEMENT}
        elements={profiles}
        noFoundMessage="No hay perfiles"
        isBusy={false}
        onChangePage={() => {}}
        totalPages={1}
        page={1}
      />
    </DataBox>
  );
};

export default ProfileManagement;
