import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { useI18n } from 'services/i18n';
import DropDownSelect from 'components/dropdown-select';
import DateRange from 'components/date-range';
import Box from 'primitive-components/box';

import Button from 'components/button';

const Container = styled(Box)`
  padding: 1.25rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 100%;
`;
// Wrappers
const WrapperDateRange = ({ selectedFilter: { dateIn, dateEnd }, onChange }) => (
  <DateRange
    value={{ dateIn, dateEnd }}
    onChange={onChange}
    mb="2rem"
  />
);
WrapperDateRange.propTypes = {
  onChange: PropTypes.func,
  selectedFilter: PropTypes.objectOf(PropTypes.any),
};

const WrapperDropDown = ({ fieldName, selectedFilter, ...remainderProps }) => (
  <DropDownSelect
    value={selectedFilter[fieldName]}
    fieldName={fieldName}
    {...remainderProps}
  />
);
WrapperDropDown.propTypes = {
  fieldName: PropTypes.string,
  selectedFilter: PropTypes.objectOf(PropTypes.any),
};

const filtersCmp = {
  dropdown: WrapperDropDown,
  date: WrapperDateRange,
};

// Main Component
const Filter = ({ config, selected, onSubmit }) => {
  const [selectedFilter, setSelectedFilter] = useState({});
  const t = useI18n();

  const handleChangeFilter = (change) => setSelectedFilter({ ...selectedFilter, ...change });
  const handleSubmitFilter = () => onSubmit?.(selectedFilter);

  useEffect(() => {
    setSelectedFilter(selected);
  }, [selected]);

  return (
    <Container>
      {config.map((conf, index) => {
        const Component = filtersCmp[conf.type];
        return Component
          ? <Component key={index} onChange={handleChangeFilter} selectedFilter={selectedFilter} {...conf} />
          : null;
      })}

      <Button
        design="info"
        p="10px 20px"
        mb="10px"
        height="fit-content"
        onClick={handleSubmitFilter}
      >
        {t('apply')}
      </Button>
    </Container>
  );
};

Filter.propTypes = {
  config: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      type: PropTypes.oneOf(['dropdown', 'date']),
      fieldName: PropTypes.string, // Only for Dropdown
      enableSearch: PropTypes.bool, // Only for Dropdown
      items: PropTypes.arrayOf(PropTypes.string), // Only for Dropdown
    }).isRequired,
  ),
  selected: PropTypes.objectOf(PropTypes.any),
  onSubmit: PropTypes.func,
};

export default Filter;
