import { Auth, Amplify } from 'aws-amplify';
import axios from 'axios';
import { paramsToSearch } from 'services/search/helpers';

import authConfig from './authConfig';

Amplify.configure(authConfig);

export default () => {
  // AUTH
  const signIn = ({ email: username, password }) => Auth.signIn({ username, password });
  const signUp = ({ email: username, password }) => Auth.signUp({ username, password });
  const signOut = () => Auth.signOut();

  // API REST
  const getClient = () => axios.create({
    baseURL: 'https://6zxv6s0rba.execute-api.us-east-2.amazonaws.com',
    paramsSerializer: paramsToSearch,
  });

  const searchDocs = (params) => getClient().get('/dev/search', { params });

  const fetchRelatedDocs = (params) => getClient().get('/dev/related', { params });

  return ({
    signIn,
    signUp,
    signOut,

    searchDocs,
    fetchRelatedDocs,
  });
};
