import { reducer as authReducer, NAME as authName } from './auth';
import { reducer as docsReducer, NAME as docsName } from './docs';
import { reducer as pcReducer, NAME as pcName } from './pc';
import { reducer as relDocsReducer, NAME as relDocsName } from './rel-docs';
import { reducer as configReducer, NAME as configName } from './config';
import { reducer as layoutReducer, NAME as layoutName } from './layout';
import { reducer as modalReducer, NAME as modalName } from './modal';

export default {
  [authName]: authReducer,
  [docsName]: docsReducer,
  [pcName]: pcReducer,
  [relDocsName]: relDocsReducer,
  [configName]: configReducer,
  [layoutName]: layoutReducer,
  [modalName]: modalReducer,
};
