import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import { cfdiPropType } from 'helpers/propTypes';
import DataBox from 'components/data-box';
import { actions, selectors } from 'store/rel-docs';
import TableBox, { tableTypes, tableSizes } from 'components/table-box';

const RelatedDocs = ({ doc }) => {
  const [page, setPage] = useState(1);
  const dispatch = useDispatch();

  const [
    isBusy,
    relatedDocs,
    totalPages,
  ] = useSelectors(
    selectors.getIsBusy,
    selectors.getRelDocs,
    selectors.getTotalPages,
  );

  const { uuid, type } = doc;
  const handleFetchRelatedDocs = () => dispatch(actions.FetchRelDocs({ page, uuid, typedoc: type }));

  useEffect(() => {
    if (doc) handleFetchRelatedDocs();
  }, [doc, page]);

  return (
    <DataBox title="related_docs" separator width="50rem">
      <TableBox
        tableType={tableTypes.RELATED_DOCS}
        tableSize={tableSizes.XS}
        elements={relatedDocs}
        noFoundMessage="no_related_docs_found"
        isBusy={isBusy}
        onChangePage={setPage}
        totalPages={totalPages}
        page={page}
      />
    </DataBox>
  );
};

RelatedDocs.propTypes = {
  doc: cfdiPropType,
};
export default RelatedDocs;
